/*=====================================
=            Topbar styles            =
=====================================*/
.topbar-notice {
	position: absolute;
	color: #ffffff;
	height: 40px;
	width: 100%;
	z-index: 150;
	@include prefix( box-shadow, $box_shadow_6, o moz webkit );

	> div {
		display: flex;
		align-items: center;
		justify-content: center;
		text-align: center;

		span.topbar-message-text {
			letter-spacing: 3px;
			@include font-size( .7 );
		}
	}

	@include media-breakpoint( md ) {

		span.topbar-message-text {
			letter-spacing: 4px;
			@include font-size( .9 );
		}
	}
}
/*=====  End of Topbar styles  ======*/

/*==================================================
=            header branding and navbar            =
==================================================*/
.site-header {
	position: fixed;
	padding: 0 15px;
	width: 100%;
	background: #000;
	z-index: 9999;
	@include prefix( transition, all .3s ease-in-out, o moz webkit );

	.brand-nav-bar {
		height: 60px;
		display: flex;
		align-items: center;
		justify-content: center;
		@include prefix( transition, all .6s ease-in-out, o moz webkit );

		.branding {
			height: 100%;
			display: flex;
			align-items: center;

			.site-title {
				text-align: start;
				font-size: 1.3rem;

				a {
					font-family: $font__title;
					color: #ffffff;
					text-decoration: none;
					// &:hover {
					// 	color: rgb(160, 160, 160);
					// }
				}
			}

      .site-description {
        position: absolute;
				clip: rect(1px, 1px, 1px, 1px);
      }

			img {
				-webkit-filter: drop-shadow( 1px 1px 2px rgba(#000, .6 ));
				filter: drop-shadow( 1px 1px 2px rgba(#000, .6 ) );
				padding: 15px;
				max-height: 105px;
				width: auto;
			}
		}
	}

	@include media-breakpoint( xxs ) {
		.brand-nav-bar .branding .site-title {
			font-size: 1.65rem;
		}
	}

	// @include media-breakpoint( sm ) {
	// 	.brand-nav-bar .branding .site-title {
	// 		font-size: 1.85rem;
	// 	}
	// }

	@include media-breakpoint( lg ) {
		padding: 0 25px;
	}
}

.home {

	.site-header {
		background: #000;
		
		&.transparent-header {
			background: #000;
		}
		li i.fa-search,
		li i.fa-shopping-cart {
			font-size: 20px;
		}
	}

	#content {
		padding: 0;
	}

	@include media-breakpoint( lg ) {
		.site-header {
			background: transparent;
		}
	}
}

/* The sticky class is added to the header with JS when it reaches its scroll position */
.home .sticky {
	position: fixed;
	left: 0;
	top: 0;
	right: 0;
	width: 100%;
	height: 30px;
	overflow: hidden;
	z-index: 9999;
}

/* Add some top padding to the page content to prevent sudden quick movement (as the header gets a new position at the top of the page (position:fixed and top:0) */
.home .sticky + .content {
  	padding-top: 120px;
}
/*=====  End of header branding and navbar  ======*/