.main-navigation {
	position: relative;
	clear: both;
	z-index: 100;
	@include prefix( transition, $trans_25, webkit moz o);

	button {
		position: absolute;
		top: -3.4rem;
		right: 5px;
		background: transparent;
		border: none;
		@include prefix( transition, $trans_25, webkit moz o);

		.hang-a-bur {
			position: relative;
			display: block;
			width: 38px;
			height: 3.4px;
			margin: 6px 0;
			opacity: 1;
			background: #ffffff;
			@include prefix( transition, $trans_25, webkit moz o);

			&-top {
				top: 0;
			}
		
			&-bottom {
				bottom: 0;
			}
		}
		
		&[aria-expanded="true"] {
		
			.hang-a-bur-top {
				top: 0.6rem;
				@include prefix( transform, rotate(220deg), webkit moz o );
			}
		
			.hang-a-bur-mid {
				opacity: 0;
			}
		
			.hang-a-bur-bottom {
				bottom: 0.6rem;
				@include prefix( transform, rotate(-220deg), webkit moz o );
			}
		}
		
		&:focus {
			outline: 0;
		}

		// @include media-breakpoint( sm ) {
		// 	top: -2.7rem;
		// }
	}

	> div {
		height: 0;
		overflow: hidden;
		width: 100%;
		@include prefix( transition, $trans_45, webkit moz o);

		// Stoped cleaning code here
		> ul {
			position: relative;
			display: block;
			list-style: none;
			margin: 0;
			padding: 0;
			overflow: hidden;
			z-index: 100;
			background: rgba( #000, .85 );
			@include prefix( transition, $trans_25, webkit moz o);
			@include prefix( box-shadow, 0 1px 6px 0 rgba(#000, 0.4), webkit moz o );

			> li {
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				flex-wrap: wrap;
				padding: 4px 8px;

				a {
					width: 100%;
					color: white;
				}

				ul {
					position: relative;
					display: block;
					list-style: none;
					margin: 0;
					left: 0;
					width: 100%;
					padding: 0;
					// opacity: 0;
					overflow: hidden;
					border-radius: 2px;
					z-index: -1;
					@include prefix( transition, $trans_25, webkit moz o);
					@include prefix( box-shadow, 0 1px 6px 0 rgba(#000, 0.4), webkit moz o );

					li {
						padding: 4px 8px;
						background: rgba( #777, .9 );
					}
				}
			}

			li {
				position: relative;
				
				&:hover {

					ul {
						top: 0;
						height: auto;
						opacity: 1;
						z-index: 999;
					}
				}
			}

			a {
				color: #000;
				font-family: $font__mont;
				font-weight: 600;
				font-size: 1rem;
				padding: 4px 0;
				white-space: nowrap;
				text-decoration: none;
				@include prefix( text-shadow, 1px 1px 2px rgba(#000, .8 ), o moz webkit );
				@include prefix( transition, $trans_25, webkit moz o);

				.wonka-cart-badge {
					display: flex;
					align-items: center;
					justify-content: center;
					font-family: 'Open Sans';
					font-weight: 800;
					background: #ffffff;
					width: 24px;
					height: 24px;
					line-height: 1.5;
					text-shadow: none;
					color: #333333;
					@include font-size( .9 );
					text-shadow: none;
					@include prefix( box-shadow, 0 1px 6px 0 rgba(#000, .6 ), o moz webkit );
				}

				&:hover {
					color: $color__main-menu-hover;
				}
			}
			
			// .current_page_item > a,
			// .current-menu-item > a {
			// 	color: #ded492; 
			// }

			.menu-item-has-children > a {

				&:after {
					content: "\f107";
					position: relative;
					font-family: "FontAwesome";
					color: #ffffff;
					@include font-size( 1.2 );
					line-height: 0.5;
					padding: 0 8px 4px;
					right: 0;
					@include prefix( text-shadow, 1px 1px 4px rgba(#000, .7 ), o moz webkit );
					@include prefix( transition, $trans_25, webkit moz o);
				}

				&:hover {

					&:after {
						color: #dad3a2;
					}
				}
			}
		}

		@include media-breakpoint( lg ) {
			height: 100%;

			> ul {
				display: flex;
				justify-content: flex-end;
				background: transparent;
				overflow: visible;
				top: 0;
				height: 100%;
				@include prefix( box-shadow, 0 1px 6px 0 rgba(#000, 0), webkit moz o );

				&#cart-menu-desktop {
					justify-content: flex-start;
				}

				li {
					position: relative;

					> ul {
						position: absolute;

						a {
							justify-content: flex-start;
						}
					}

					&:hover {
						> ul {
							top: 50px;
							width: auto;
						}
					}
				}
			}
		}
	}
}

/* Small menu. */
// .menu-toggle,
.main-navigation.toggled,
.main-navigation.toggled {
	div {
		height: 70px;
		overflow: hidden;
		@include prefix( transition, $trans_45, webkit moz o);
	}
} 

.main-navigation.toggled ul#cart-menu {
	top: -5px;
}

@include media-breakpoint( lg ) {
	.menu-toggle {
		display: none;
	}
}

.comment-navigation,
.posts-navigation,
.post-navigation {

	.site-main & {
		margin: 0 0 1.5em;
		overflow: hidden;
	}

	.nav-previous {
		float: left;
		width: 50%;
	}

	.nav-next {
		float: right;
		text-align: right;
		width: 50%;
	}
}

@media screen and  ( max-width: 992px ) {

	header.site-header div.brand-nav-bar {
		
		div.div-nav {
			margin-left: -30px;
			margin-right: -30px;
			padding-left: 0;
			padding-right: 0; 
		}
		.branding {
			right: 2.1rem;
		}
		ul#primary-menu {
			// top: -20px;
		}
	}
}